<template>
  <div class="black-bg" v-if="모달창열렸니 == false">
    <div class="white-bg">
      <h4>{{ 원룸들[누른거].title }}</h4>
      <p>{{ 원룸들[누른거].content }}</p>
      <p>{{ 원룸들[누른거].price }}</p>
      <button @click="모달창열렸니 = true">버튼</button>
    </div>
  </div>
  <div class="menu">
    <a v-for="(a, i) in 메뉴들" :key="i">{{ a }}</a>
  </div>
  <button @click="priceSort">가격순 정렬</button>
  <button @click="sortBack">되돌리기</button>
  <CardHello @openModal="모달창열렸니 = false; 누른거=$event" :원룸들="원룸들"/>

  
</template>

<script>
import data from './assets/oneroom.js';
import CardHello from './CardHello.vue'

export default {
  name: 'App',
  data(){
    return{
      원룸들오리지널 : [...data],
      누른거 : 0,
      원룸들 : data,
      모달창열렸니 : true,
      신고수 : [0, 0, 0],
      메뉴들 : ['Home', 'Shop', 'About'],
      prices : [60, 80, 70],
      products : ['용현동', '하긱동', '우동']
    }
  },

  methods : {
    priceSort(){
      this.원룸들.sort(function(a, b){
        return a.price - b.price
      });
    },
    sortBack(){
      this.원룸들 = this.원룸들오리지널;
    }
    
  },
  components: {
    CardHello
  }
}
</script>

